/***/ (function(module, exports) {

	module.exports = {"data":{"postsJson":{"title":"พระเจ้ามาหาเรา","questionsTitle":"หิวกระหายที่จะรู้จักพระเจ้ามากขึ้น","display":"block","bookName":"ยอห์น","ref":"1:1-1:51","html":"<p>   <strong>1</strong><u>1</u>คำกล่าวดำรงอยู่นับแต่ครั้งปฐมกาล คำกล่าวนั้นดำรงอยู่กับพระเจ้า และคำกล่าวนั้นคือพระเจ้า <u>2</u>พระองค์ได้ดำรงอยู่กับพระเจ้านับแต่ครั้งปฐมกาล <u>3</u>ทุกสิ่งเกิดขึ้นได้ก็เพราะพระองค์ ถ้าปราศจากพระองค์แล้ว สิ่งที่เป็นอยู่นี้จะมีขึ้นมาไม่ได้ <u>4</u>พระองค์เป็นแหล่งกำเนิดแห่งชีวิต และชีวิตนั้นเป็นความสว่างของมนุษย์ <u>5</u>ความสว่างส่องเข้ามาในความมืด และความมืดก็ไม่อาจเอาชนะความสว่างได้<br />   <u>6</u>มีชายคนหนึ่งชื่อยอห์นผู้ซึ่งพระเจ้าใช้มา <u>7</u>ท่านได้มาเพื่อเป็นพยาน และเป็นผู้ยืนยันถึงความสว่างนั้น เพื่อคนทั้งปวงจะได้มีความเชื่อโดยผ่านท่าน* <u>8</u>ท่านเองไม่ใช่ความสว่าง แต่มาเพื่อเป็นผู้ยืนยันถึงความสว่างเท่านั้น<br />   <u>9</u>ความสว่างที่แท้ได้ทอแสงไปยังมนุษย์ทุกคน กำลังเข้ามาสู่โลก <u>10</u>พระองค์ดำรงอยู่ในโลก แม้ว่าพระเจ้าสร้างโลกขึ้นมาโดยผ่านพระองค์ แต่โลกกลับไม่รู้จักพระองค์ <u>11</u>พระองค์มาสู่บ้านเมืองของพระองค์ แต่ชนชาติของพระองค์กลับไม่ต้อนรับ <u>12</u>ส่วนคนที่ต้อนรับและเชื่อในพระนามของพระองค์ พระองค์ก็ให้ได้รับสิทธิ์เป็นบุตรของพระเจ้า <u>13</u>คือบุตรที่ไม่ได้เกิดจากเลือดเนื้อที่เป็นมนุษย์หรือความต้องการฝ่ายเนื้อหนัง* หรือความประสงค์ของฝ่ายชาย แต่เกิดจากพระเจ้า<br />   <u>14</u>คำกล่าวนั้นได้มาเกิดเป็นมนุษย์และพำนักอยู่ท่ามกลางเรา พวกเราได้เห็นพระบารมีของพระองค์ อันเป็นพระบารมีของพระบุตรองค์เดียวผู้มาจากพระบิดา บริบูรณ์ด้วยพระคุณและความจริง <u>15</u>ยอห์นร้องประกาศเพื่อยืนยันถึงพระองค์ว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>พระองค์คือผู้ที่ข้าพเจ้ากล่าวถึงว่า ‘ผู้ที่มาภายหลังข้าพเจ้าคือผู้ที่เหนือยิ่งกว่าข้าพเจ้า เพราะว่าพระองค์ดำรงอยู่ก่อนข้าพเจ้า’</h4><p><u>16</u>จากความบริบูรณ์ของพระองค์ เราทุกคนจะได้รับพระคุณเพิ่มแล้วเพิ่มอีกเสมอไป <u>17</u>กฎบัญญัติที่เราได้รับนั้นผ่านมาทางโมเสส* ส่วนพระคุณและความจริงผ่านพระเยซูคริสต์* <u>18</u>ไม่มีใครเคยเห็นพระเจ้า แต่พระบุตรองค์เดียวผู้ดำรงความเป็นพระเจ้า ผู้อยู่ในอ้อมอกของพระบิดา ได้ทำให้พระบิดาเป็นที่รู้จัก<br />   <u>19</u>นี่เป็นคำยืนยันของยอห์น เมื่อชาวยิวจากเมืองเยรูซาเล็มส่งพวกปุโรหิต*และพวกเลวีไปถามว่า ท่านคือใคร* <u>20</u>ท่านยอมรับ และตอบตามความจริงว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ข้าพเจ้าไม่ใช่พระคริสต์</h4><p>   <u>21</u>พวกเขาจึงถามต่อไปว่า</p><h1>พวกปุโรหิตและเลวี</h1><h4>แล้วท่านคือใครเล่า เป็นเอลียาห์หรือ</h4><p>ท่านตอบว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ไม่ใช่</h4><p><h1>พวกปุโรหิตและเลวี</h1><h4>ท่านเป็นผู้เผยคำกล่าวของพระเจ้าผู้นั้นหรือ</h4><p>ท่านตอบว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ไม่ใช่</h4><p><u>22</u>ในที่สุดเขาเหล่านั้นพูดว่า</p><h1>พวกปุโรหิตและเลวี</h1><h4>ท่านเป็นใคร โปรดให้คำตอบแก่เราเพื่อกลับไปบอกบรรดาผู้ที่ส่งเรามาเถิด ท่านอ้างว่าท่านเป็นใคร</h4><p><u>23</u>ยอห์นตอบตามคำกล่าวของอิสยาห์ผู้เผยคำกล่าวของพระเจ้าว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ข้าพเจ้าเป็นเสียงของผู้ที่ร้องในถิ่นทุรกันดารว่า ‘จงทำทางให้ตรงเพื่อพระผู้เป็นเจ้าเถิด’*</h4><p>   <u>24</u>ส่วนคนที่พวกฟาริสีส่งมา* <u>25</u>ได้ถามยอห์นว่า</p><h1>พวกปุโรหิตและเลวี</h1><h4>ถ้าท่านไม่ใช่พระคริสต์ หรือเอลียาห์ หรือผู้เผยคำกล่าวของพระเจ้าผู้นั้น แล้วทำไมท่านจึงให้บัพติศมาเล่า*</h4><p><u>26</u>ยอห์นตอบว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ข้าพเจ้าให้บัพติศมาด้วยน้ำ แต่ผู้ที่ยืนอยู่ท่ามกลางพวกท่าน ท่านกลับไม่รู้จัก <u>27</u>พระองค์เป็นผู้ที่มาภายหลังข้าพเจ้า แม้แต่เชือกผูกรองเท้าของพระองค์ ข้าพเจ้าก็มิบังควรที่จะแก้ออก</h4><p><u>28</u>เหตุการณ์นี้เกิดขึ้นที่หมู่บ้านเบธานีทางด้านตะวันออกของแม่น้ำจอร์แดน อันเป็นที่ซึ่งยอห์นกำลังให้บัพติศมาอยู่<br />   <u>29</u>วันรุ่งขึ้นยอห์นเห็นพระเยซูกำลังเดินตรงมาหาท่าน จึงกล่าวว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ดูสิ ลูกแกะของพระเจ้า เป็นผู้ที่รับเอาบาปของโลกไป <u>30</u>นี่คือผู้ที่ข้าพเจ้าพูดถึงว่า ‘ผู้มาภายหลังข้าพเจ้ายิ่งใหญ่กว่าข้าพเจ้า เพราะพระองค์ดำรงอยู่ก่อนข้าพเจ้า’ <u>31</u>ข้าพเจ้าเองแม้ไม่รู้จักพระองค์มาก่อน แต่เหตุที่ข้าพเจ้ามาให้บัพติศมาด้วยน้ำ ก็เพื่อให้พระองค์ได้เป็นที่ประจักษ์แก่พวกชนชาติอิสราเอล</h4><p><u>32</u>แล้วยอห์นก็กล่าวยืนยันว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ข้าพเจ้าเห็นพระวิญญาณลงมาจากสวรรค์ในรูปลักษณ์ของนกพิราบ และสถิตกับพระองค์ <u>33</u>ข้าพเจ้าเองแม้ไม่รู้จักพระองค์มาก่อน แต่ผู้ที่ส่งข้าพเจ้ามาเพื่อให้บัพติศมาด้วยน้ำได้บอกข้าพเจ้าไว้ว่า ‘เมื่อเจ้าเห็นพระวิญญาณลงมาสถิตกับผู้ใด ผู้นั้นจะเป็นผู้ที่ให้บัพติศมาด้วยพระวิญญาณบริสุทธิ์’* <u>34</u>ข้าพเจ้าได้เห็นแล้ว และขอยืนยันว่า ผู้นี้เป็นพระบุตรของพระเจ้า</h4><p>   <u>35</u>ในวันรุ่งขึ้น ยอห์นยืนอยู่ที่นั่นอีกกับสาวก 2 คน <u>36</u>เมื่อท่านเห็นพระเยซูเดินผ่านไป ท่านกล่าวว่า</p><h1>ยอห์นผู้ให้บัพติศมา</h1><h4>ดูสิ ลูกแกะของพระเจ้า</h4><p><u>37</u>เมื่อสาวกทั้งสองได้ยิน ก็ติดตามพระเยซูไป <u>38</u>พระเยซูหันมาพบว่า พวกเขาเดินตามมา จึงถามว่า</p><h1>พระเยซู</h1><h2>เจ้าแสวงหาอะไร</h2><p>เขาตอบว่า</p><h1>อันดรูว์และสาวกอื่น</h1><h3>รับบี</h3><p>(ซึ่งแปลว่าอาจารย์)</p><h1>อันดรูว์และสาวกอื่น</h1><h3>ท่านพักอยู่ที่ไหน</h3><p><u>39</u>พระองค์ตอบว่า</p><h1>พระเยซู</h1><h2>มาเถิด แล้วเจ้าจะได้เห็นเอง</h2><p>ดังนั้นสาวกทั้งสองจึงได้ตามไปและเห็นว่า พระองค์พักอยู่ที่ไหน ในวันนั้นเขาก็ได้พักอยู่กับพระองค์ ขณะนั้นเป็นเวลาประมาณ 10 โมงเช้า* <u>40</u>หนึ่งในสองคนที่ได้ยินยอห์นพูดและได้ติดตามพระองค์ไปนั้น คืออันดรูว์น้องชายของซีโมนเปโตร <u>41</u>อันดรูว์จึงไปหาซีโมนพี่ชายของตนก่อนเพื่อบอกเขาว่า</p><h1>อันดรูว์</h1><h3>เราได้พบพระเมสสิยาห์แล้ว</h3><p>(ซึ่งแปลว่าพระคริสต์) <u>42</u>ครั้นแล้ว ก็พาซีโมนมาหาพระเยซู พระเยซูมองเขาและกล่าวว่า</p><h1>พระเยซู</h1><h2>เจ้าคือซีโมนบุตรของยอห์น เจ้าจะได้รับชื่อว่าเคฟาส</h2><p>(ซึ่งแปลว่าเปโตร)<br />   <u>43</u>วันรุ่งขึ้นพระเยซูตั้งใจจะไปยังแคว้นกาลิลี พระองค์พบฟีลิปจึงกล่าวขึ้นว่า</p><h1>พระเยซู</h1><h2>จงตามเรามาเถิด</h2><p><u>44</u>ฟีลิปมาจากเมืองเบธไซดา เช่นเดียวกับอันดรูว์และเปโตร <u>45</u>ฟีลิปพบนาธานาเอลและบอกเขาว่า</p><h1>ฟีลิป</h1><h3>เราได้พบผู้ที่โมเสสเขียนถึงในหมวดกฎบัญญัติ และที่บรรดาผู้เผยคำกล่าวของพระเจ้าเขียนถึงด้วย คือพระเยซูแห่งเมืองนาซาเร็ธผู้เป็นบุตรของโยเซฟ</h3><p><u>46</u>นาธานาเอลถามว่า</p><h1>นาธานาเอล</h1><h3>สิ่งดีอันใดจะมาจากเมืองนาซาเร็ธได้หรือ</h3><p>ฟีลิปบอกเขาว่า</p><h1>ฟีลิป</h1><h3>มาดูสิ</h3><p><u>47</u>เมื่อพระเยซูเห็นนาธานาเอลเดินเข้ามาใกล้ พระองค์จึงกล่าวว่า</p><h1>พระเยซู</h1><h2>คนนี้เป็นชาวอิสราเอลแท้ หามีเล่ห์เหลี่ยมไม่</h2><p><u>48</u>นาธานาเอลจึงถามว่า</p><h1>นาธานาเอล</h1><h3>ท่านรู้จักข้าพเจ้าได้อย่างไร</h3><p>พระเยซูตอบว่า</p><h1>พระเยซู</h1><h2>เราเห็นเจ้าเวลาเจ้าอยู่ใต้ต้นมะเดื่อก่อนที่ฟีลิปจะไปเรียกเสียอีก</h2><p><u>49</u>นาธานาเอลตอบพระองค์ว่า</p><h1>นาธานาเอล</h1><h3>รับบี พระองค์เป็นพระบุตรของพระเจ้า พระองค์เป็นกษัตริย์ของอิสราเอล</h3><p><u>50</u>พระเยซูกล่าวว่า</p><h1>พระเยซู</h1><h2>เจ้าเชื่อเพราะเราบอกว่า เราเห็นเจ้าอยู่ใต้ต้นมะเดื่อ เจ้าจะเห็นสิ่งที่ยิ่งใหญ่กว่านี้</h2><p><u>51</u>พระองค์กล่าวต่อไปอีกว่า</p><h1>พระเยซู</h1><h2>เราขอบอกความจริงกับเจ้าว่า เจ้าจะเห็นสวรรค์เปิด และบรรดาทูตสวรรค์*ของพระเจ้าจะขึ้นและลงอยู่เหนือบุตรมนุษย์*</h2><h5>*1:7 เชื่อ คำนี้นอกจากจะมีความหมายว่า เชื่อในพระเจ้า แล้วยังหมายถึง “การวางใจ” ด้วย และคำว่า “เชื่อ” นี้ได้กล่าวไว้ในฉบับยอห์นเกือบ 100 ครั้ง<br />*1:13 ฝ่ายเนื้อหนังในที่นี้เป็นสิ่งตรงข้ามกับ “ฝ่ายวิญญาณ” คือความต้องการที่มักจะโน้มเอียงไปในทางไม่ดี ซึ่งมนุษย์ทุกคนมีโดยธรรมชาติ<br />*1:17a โมเสส เป็นผู้ที่นำชาวยิวที่เป็นทาสออกจากประเทศอียิปต์ไปประเทศอิสราเอล ประมาณ 1,500 ปีก่อน ค.ศ. (1,000 ปี ก่อน พ.ศ.)<br />*1:17b เยซู ภาษาฮีบรูแปลได้ความว่า พระเจ้าผู้ช่วยให้รอดพ้น ส่วนคำว่า “คริสต์” เป็นภาษากรีกที่แปลจากภาษาฮีบรู “เมสสิยาห์” ชาวยิวเข้าใจว่า เมสสิยาห์คือผู้ที่ได้รับเลือกและเป็นผู้ช่วยให้รอดพ้นที่พระเจ้าสัญญาไว้ในพันธสัญญาเดิม<br />*1:19a ปุโรหิต ผู้ที่ทำงานประจำพระวิหาร<br />*1:19b พวกเลวี มีหน้าที่ช่วยพวกปุโรหิตทำงานในพระวิหาร<br />*1:23 ฉบับอิสยาห์ 40:3<br />*1:24 ฟาริสี มีความหมายว่า “คนที่แยกออกจากผู้อื่น” เป็นกลุ่มผู้นำชาวยิวซึ่งมีหน้าที่ปฏิบัติตนตามกฎบัญญัติเพื่อเป็นตัวอย่าง<br />*1:25 พิธีบัพติศมาคือการจุ่มตัวลงในน้ำ เป็นสัญลักษณ์ของผู้ที่ตั้งใจจะกลับใจจากการทำบาป<br />*1:33 พระวิญญาณบริสุทธิ์ พระวิญญาณพระเจ้า<br />*1:39 หรือ 4 โมงเย็น<br />*1:51a ทูตสวรรค์ ตัวแทนหรือผู้ส่งข่าวจากสวรรค์<br />*1:51b บุตรมนุษย์ ชื่อนี้มาฉบับเอเสเคียล และดาเนียล แสดงว่าถึงแม้พระเยซูเป็นพระเจ้า พระองค์ตั้งใจที่จะเป็นมนุษย์ เพื่อช่วยมนุษย์ให้รอดพ้น</h5>","path":"/336","Question1":"สังเกตุสิ่งที่พระเจ้าทำในเรื่องนี้","Question2":"สิ่งทีพระเจ้าทำสำแดงให้เราเห็นถึงพระลักษณะด้านใดของพระองค์","Question3":"ท่านเคยมีประสบการณ์ถึงพระลักษณะด้านนี้ของพระเจ้าหรือไม่ ถ้าเคย โปรดอธิบายเกี่ยวกับประสบการณ์นั้น ถ้าไม่เคย  ท่านจะสามารถมีประสบการณ์ถึงสิ่งนี้ได้อย่างไร","Question4":"ท่านสามารถจะทำอะไรเพื่อที่จะทำให้ท่านอยากรู้จักกับพระเจ้ามากยิ่งขึ้นและมีความสนิทสนมกับพระองค์ได้ในวันนี้","prevPath":"/335","nextPath":"/337"}},"pathContext":{}}

/***/ })

